.Dialogs {
    display: grid;
    grid-template-areas: "d m";
    grid-template-columns: 2fr 10fr;
    color: darkolivegreen;
}

.DialogItems {
    grid-area: d;
}

.DialogItem {

}

.DialogItem.Active {
    color: wheat;
}

.MessageItems {
    grid-area: m;
}

.MessageItem {
    display: grid;
    grid-template-areas: "message avatar";
    grid-template-columns: 12fr 1fr;
    margin: 0.5vw;
}
